import HomePage from '../components/pages/home';

const LensPlayground = () => import(/* webpackChunkName: "playground" */ '../components/pages/lens-playground');

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomePage,
    meta: {
      title: 'Lens Home',
      subtitle: 'Home'
    }
  },
  {
    path: '/playground',
    name: 'playground',
    component: LensPlayground,
    meta: {
      title: 'Lens Playground',
      subtitle: 'Playground'
    }
  }
];

export default routes;