<template>
    <VApp>
        <VNavigationDrawer
                v-model="drawer"
                app
                clipped
                temporary
        >
            <VList dense>
                <VListItem
                        v-for="({title, to, icon, href, target}) in menuItems"
                        :key="title"
                        :to="to"
                        :href="href"
                        :target="target"
                        link
                        exact
                >
                    <VListItemAction v-if="icon">
                        <VIcon>{{ icon }}</VIcon>
                    </VListItemAction>

                    <VListItemContent>
                        <VListItemTitle>{{ title }}</VListItemTitle>
                    </VListItemContent>
                </VListItem>
            </VList>
        </VNavigationDrawer>

        <VAppBar
                app
                clipped-left
        >
            <VAppBarNavIcon @click.stop="drawer = !drawer" />

            <RouterLink :to="{path: '/'}" class="d-flex align-center">
                <VImg
                        :src="require('../../img/lens-logo-96x96.png').default"
                        alt="Lens Logo"
                        height="48"
                        width="48"
                />
            </RouterLink>

            <VToolbarTitle class="ml-2">
                LENS <small>{{ $route.meta.subtitle }}</small>
            </VToolbarTitle>

            <VSpacer/>
        </VAppBar>

        <VContent>
            <RouterView/>
        </VContent>

        <VFooter app>
            <span class="grey--text body-2">&copy; 2020 Alexander Alexandrov (alxcube)</span>
        </VFooter>

        <VSnackbar color="error" top v-model="showErrorMessage">
            {{ errorMessage }}
        </VSnackbar>
    </VApp>
</template>

<script>
    const menuItems = [
      {
        title: 'Home',
        to: {name: 'home'}
      },
      {
        title: 'Playground',
        to: {name: 'playground'}
      },
      {
        title: 'Documentation',
        href: 'docs',
        target: '_blank'
      },
      {
        title: 'GitHub',
        href: 'https://github.com/alxcube/lens',
        target: '_blank'
      }
    ];

    export default {
      name: 'AppLayout',

      data() {
        return {
          drawer: false,
          showErrorMessage: false,
          errorMessage: null,
          menuItems
        };
      },

      errorCaptured(err, vm, info) {
        this.errorMessage = err;
        this.showErrorMessage = true;
      }
    }
</script>