<template>
    <VContainer fluid class="home" fill-height pa-0>
        <section class="hero pa-6">
            <VImg
                    :src="require('../../../../img/lens-logo.png').default"
                    max-width="406"
                    max-height="406"
                    class="hero__image"
            />

            <div class="hero__text">
                <div class="hero__text-wrap">
                    <h1 class="display-4">LENS</h1>

                    <p class="headline">
                        Pure JavaScript library for image distortions with
                        <a href="https://imagemagick.org/index.php" target="_blank">ImageMagick</a> algorithms.
                    </p>
                </div>
            </div>

            <div class="hero__buttons text-center">
                <VBtn color="#0f3a53" x-large href="docs" target="_blank" class="mb-2 mx-3" width="200">
                    Documentation
                </VBtn>

                <VBtn color="#98841a" x-large :to="{name: 'playground'}" class="mb-2 mx-3" width="200">
                    Playground
                </VBtn>

                <VBtn color="#280d3e" x-large href="https://github.com/alxcube/lens" target="_blank" class="mb-2 mx-3" width="200">
                    GitHub
                </VBtn>
            </div>
        </section>

        <VContainer>
            <section class="intro py-12">
                <VRow>
                    <VCol cols="12" sm="8" lg="9" xl="10">
                        <h2 class="display-2 mb-4">Introduction</h2>

                        <p>
                            Lens is JavaScript library for raster image distortions using
                            <a href="https://www.imagemagick.org/Usage/distorts/#mapping" target="_blank">reverse pixel mapping</a>
                            technique.
                        </p>

                        <p>
                            It supports Elliptical Weighted Average resampling, supersampling, resampling filters,
                            virtual pixels and other features.
                        </p>

                        <p>
                            Lens uses algorithms ported from great
                            <a href="https://imagemagick.org/index.php" target="_blank">ImageMagick</a> software, so it
                            is very easy to use if you are familiar with ImageMagick distortions.
                            However, if you are new to image distortions,
                            you can find many useful info in
                            <a href="docs/tutorial-02.00.basic-concepts.html" target="_blank">Basic Concepts</a> section of library
                            <a href="docs" target="_blank">documentation</a>.
                        </p>

                        <p>
                            Lens is written in pure JavaScript and has no dependencies.
                        </p>
                    </VCol>

                    <VCol cols="12" sm="4" lg="3" xl="2">
                        <VImg
                                :src="require('../../../../img/ImageMagick_logo.svg').default"
                                width="273"
                                height="283"
                                max-width="100%"
                                contain
                        />
                    </VCol>
                </VRow>
            </section>

            <section class="distortions py-12">
                <h2 class="display-2 mb-4">Supported distortions</h2>

                <p>
                    Following distortions are supported at the moment. More will be added in the future. You can also
                    make your own distortion by implementing <a href="docs/DistortionInterface.html" target="_blank">distortion interface</a>.
                </p>

                <VRow>
                    <VCol cols="12" sm="4" v-for="({title, img, paragraphs, details}, index) in supportedDistortions" :key="index">
                        <VCard>
                            <VImg
                                    :src="img"
                                    height="252"
                                    contain
                            />

                            <VCardTitle>
                                {{ title }}
                            </VCardTitle>

                            <VCardText>
                                <p v-for="(paragraph, pindex) in paragraphs" :key="pindex">
                                    {{ paragraph }}
                                </p>
                            </VCardText>

                            <VCardActions>
                                <VBtn :href="details" target="_blank">
                                    Details
                                </VBtn>
                            </VCardActions>
                        </VCard>
                    </VCol>
                </VRow>
            </section>

            <section class="environments">
                <h2 class="display-2 mb-4">Environments</h2>

                <VRow>
                    <VCol cols="12" sm="6">
                        <VCard color="transparent" flat>
                            <div class="d-flex">
                                <VImg
                                        :src="require('../../../../img/browsers.svg').default"
                                        width="568"
                                        height="360"
                                        max-width="100%"
                                        contain
                                />
                            </div>

                            <VCardTitle>
                                Browsers
                            </VCardTitle>

                            <VCardText>
                                <p>
                                    Out of the box Lens works in all modern browsers.
                                </p>

                                <p>
                                    With few polyfills Lens will work in all older browsers with HTML5 canvas support.
                                </p>
                            </VCardText>
                        </VCard>
                    </VCol>

                    <VCol cols="12" sm="6">
                        <VCard color="transparent" flat>
                            <div class="d-flex">
                                <VImg
                                        :src="require('../../../../img/nodejs-new-pantone-white.svg').default"
                                        width="568"
                                        height="360"
                                        max-width="100%"
                                        contain
                                />
                            </div>

                            <VCardTitle>
                                Node.js
                            </VCardTitle>

                            <VCardText>
                                <p>
                                    Lens can be used in Node.js in combination with other library which provides
                                    pixel-level manipulations, eg
                                    <a href="https://www.npmjs.com/package/jimp" target="_blank">Jimp</a>.
                                </p>

                                <p>
                                    You can find Jimp adapter <a href="https://github.com/alxcube/lens-jimp" target="_blank">here</a>.
                                </p>
                            </VCardText>
                        </VCard>
                    </VCol>
                </VRow>
            </section>
        </VContainer>
    </VContainer>
</template>

<script>
  const supportedDistortions = [{
    title: 'Affine',
    img: require('../../../../img/distortions/affine.png').default,
    paragraphs: [
      'Affine distortion incorporates scaling, rotation, translation and shearing of image.'
    ],
    details: 'docs/tutorial-03.01.affine.html'
  }, {
    title: 'Perspective',
    img: require('../../../../img/distortions/perspective.png').default,
    paragraphs: [
      'Perspective distortion makes 3D effect that some parts of image are looking closer to the viewer, and other parts are looking further.'
    ],
    details: 'docs/tutorial-03.02.perspective.html'
  }, {
    title: 'Arc',
    img: require('../../../../img/distortions/arc.png').default,
    paragraphs: [
      'Arc distortion "bends" image into arc of given angle.'
    ],
    details: 'docs/tutorial-03.03.arc.html'
  }];

  export default {
    name: 'HomePage',

    data() {
      return {
        supportedDistortions
      };
    }
  }
</script>

<style scoped lang="scss">
    .hero {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-content: center;
        width: 100%;

        background: radial-gradient(circle at 80% 80%, #02223d 20%, transparent 80%),
        radial-gradient(circle at 60% 10%, #5d4b01 30%, transparent),
        radial-gradient(circle at 10% 75%, #11011d 30%, transparent);

        @media (min-width: 875px) {
            height: calc(100vh - 96px);
        }
    }

    .hero__image {
        flex-shrink: 1;
    }

    .hero__text {
        flex-basis: 50%;
        flex-shrink: 1;
        z-index: 1;
        flex-grow: 1;

        @media (min-width: 875px) {
            flex-grow: 0;
        }

        .hero__text-wrap {
            @media (min-width: 875px) {
                padding-top: 60px;
                margin-left: -150px;
            }
        }
    }

    .hero__buttons {
        flex-basis: 100%;
    }
</style>